// src/app/notifications/page.tsx
"use client";

import { useState, useEffect } from 'react';
import { Bell, Settings, MessageSquare, Heart, User, BookOpen, Sun, Moon, ChevronLeft, Trash2, Check } from 'lucide-react';

// Define notification types
type NotificationType = 'like' | 'comment' | 'follow' | 'mention' | 'system';

interface Notification {
  id: string;
  type: NotificationType;
  message: string;
  timestamp: string;
  read: boolean;
  userImage?: string;
  postTitle?: string;
}

export default function NotificationsPage() {
  const [notifications, setNotifications] = useState<Notification[]>([]);
  const [filter, setFilter] = useState<NotificationType | 'all'>('all');
  const [loading, setLoading] = useState(true);
  const [darkMode, setDarkMode] = useState(false);

  useEffect(() => {
    // Check if user prefers dark mode
    if (typeof window !== 'undefined') {
      const isDarkMode = window.matchMedia('(prefers-color-scheme: dark)').matches;
      setDarkMode(isDarkMode);
    }
    
    // Simulate fetching notifications from an API
    const fetchNotifications = async () => {
      setLoading(true);
      try {
        // This would be a real API call in a production app
        await new Promise(resolve => setTimeout(resolve, 800));
        
        // Sample notification data
        const mockNotifications: Notification[] = [
          {
            id: '1',
            type: 'like',
            message: 'Sarah Johnson liked your article "Getting Started with Next.js"',
            timestamp: '2025-04-17T10:30:00Z',
            read: false,
            userImage: '/api/placeholder/40/40',
            postTitle: 'Getting Started with Next.js'
          },
          {
            id: '2',
            type: 'comment',
            message: 'Alex Chen commented on your article "Mastering Tailwind CSS"',
            timestamp: '2025-04-16T18:45:00Z',
            read: true,
            userImage: '/api/placeholder/40/40',
            postTitle: 'Mastering Tailwind CSS'
          },
          {
            id: '3',
            type: 'follow',
            message: 'Miguel Rodriguez started following you',
            timestamp: '2025-04-16T14:20:00Z',
            read: false,
            userImage: '/api/placeholder/40/40'
          },
          {
            id: '4',
            type: 'system',
            message: 'Your article "TypeScript Best Practices" has been published',
            timestamp: '2025-04-15T09:10:00Z',
            read: true,
            postTitle: 'TypeScript Best Practices'
          },
          {
            id: '5',
            type: 'mention',
            message: 'Lisa Wong mentioned you in a comment',
            timestamp: '2025-04-14T16:30:00Z',
            read: false,
            userImage: '/api/placeholder/40/40'
          },
          {
            id: '6',
            type: 'like',
            message: 'David Smith and 5 others liked your article "React Hooks Explained"',
            timestamp: '2025-04-14T11:15:00Z',
            read: true,
            userImage: '/api/placeholder/40/40',
            postTitle: 'React Hooks Explained'
          },
          {
            id: '7',
            type: 'system',
            message: 'Your blog has reached 1,000 subscribers!',
            timestamp: '2025-04-13T20:00:00Z',
            read: true
          },
          {
            id: '8',
            type: 'comment',
            message: 'Jessica Lee replied to your comment on "Building a Blog with Next.js"',
            timestamp: '2025-04-12T13:45:00Z',
            read: false,
            userImage: '/api/placeholder/40/40',
            postTitle: 'Building a Blog with Next.js'
          }
        ];
        
        setNotifications(mockNotifications);
      } catch (error) {
        console.error('Error fetching notifications:', error);
      } finally {
        setLoading(false);
      }
    };
    
    fetchNotifications();
  }, []);

  const formatTimestamp = (timestamp: string): string => {
    const date = new Date(timestamp);
    const now = new Date();
    const diffTime = Math.abs(now.getTime() - date.getTime());
    const diffDays = Math.floor(diffTime / (1000 * 60 * 60 * 24));
    
    if (diffDays === 0) {
      return 'Today';
    } else if (diffDays === 1) {
      return 'Yesterday';
    } else if (diffDays < 7) {
      return `${diffDays} days ago`;
    } else {
      return date.toLocaleDateString('en-US', { month: 'short', day: 'numeric' });
    }
  };

  const getNotificationIcon = (type: NotificationType) => {
    switch (type) {
      case 'like':
        return <Heart className={`h-5 w-5 ${darkMode ? 'text-pink-400' : 'text-pink-500'}`} />;
      case 'comment':
        return <MessageSquare className={`h-5 w-5 ${darkMode ? 'text-blue-400' : 'text-blue-500'}`} />;
      case 'follow':
        return <User className={`h-5 w-5 ${darkMode ? 'text-green-400' : 'text-green-500'}`} />;
      case 'mention':
        return <MessageSquare className={`h-5 w-5 ${darkMode ? 'text-purple-400' : 'text-purple-500'}`} />;
      case 'system':
        return <BookOpen className={`h-5 w-5 ${darkMode ? 'text-amber-400' : 'text-amber-500'}`} />;
    }
  };

  const markAllAsRead = () => {
    setNotifications(prev => prev.map(notification => ({ ...notification, read: true })));
  };

  const toggleDarkMode = () => {
    setDarkMode(!darkMode);
  };

  const deleteNotification = (id: string) => {
    setNotifications(prev => prev.filter(notification => notification.id !== id));
  };

  const markAsRead = (id: string) => {
    setNotifications(prev => 
      prev.map(notification => 
        notification.id === id ? { ...notification, read: true } : notification
      )
    );
  };

  const filteredNotifications = filter === 'all' 
    ? notifications 
    : notifications.filter(notification => notification.type === filter);

  const unreadCount = notifications.filter(notification => !notification.read).length;

  // Apply dynamic classes based on dark mode
  const bgColor = darkMode ? 'bg-gray-900' : 'bg-gray-50';
  const cardBg = darkMode ? 'bg-gray-800' : 'bg-white';
  const borderColor = darkMode ? 'border-gray-700' : 'border-gray-200';
  const textColor = darkMode ? 'text-gray-100' : 'text-gray-900';
  const textMuted = darkMode ? 'text-gray-400' : 'text-gray-500';
  const highlightBg = darkMode ? 'bg-blue-900/20' : 'bg-blue-50';
  const buttonHover = darkMode ? 'hover:bg-gray-700' : 'hover:bg-gray-100';
  const unreadBg = darkMode ? 'bg-blue-900/30' : 'bg-blue-50';

  return (
    <div className={`min-h-screen ${bgColor} transition-colors duration-200`}>
      <div className="max-w-4xl mx-auto pt-8 px-4 sm:px-6 lg:px-8">
        {/* Back button + theme toggle */}
        <div className="flex justify-between items-center mb-6">
          <button className={`${textColor} flex items-center text-sm font-medium hover:opacity-80`}>
            <ChevronLeft className="h-4 w-4 mr-1" />
            Back to Blog
          </button>
          <button 
            onClick={toggleDarkMode} 
            className={`p-2 rounded-full ${darkMode ? 'bg-gray-800' : 'bg-gray-100'} ${buttonHover}`}
          >
            {darkMode ? (
              <Sun className="h-5 w-5 text-amber-400" />
            ) : (
              <Moon className="h-5 w-5 text-indigo-600" />
            )}
          </button>
        </div>
        
        <div className={`${cardBg} rounded-xl shadow-lg overflow-hidden border ${borderColor} transition-colors duration-200`}>
          {/* Header */}
          <div className={`px-6 py-5 border-b ${borderColor} flex justify-between items-center`}>
            <div className="flex items-center space-x-3">
              <div className={`p-2 rounded-full ${darkMode ? 'bg-indigo-900/30' : 'bg-indigo-100'}`}>
                <Bell className={`h-6 w-6 ${darkMode ? 'text-indigo-400' : 'text-indigo-600'}`} />
              </div>
              <div>
                <h1 className={`text-xl font-bold ${textColor}`}>Notifications</h1>
                <p className={`text-sm ${textMuted}`}>Stay updated with your blog activity</p>
              </div>
            </div>
            <div className="flex items-center space-x-4">
              {unreadCount > 0 && (
                <button 
                  onClick={markAllAsRead}
                  className={`text-sm ${darkMode ? 'text-indigo-400 hover:text-indigo-300' : 'text-indigo-600 hover:text-indigo-800'} font-medium flex items-center transition-colors`}
                >
                  <Check className="h-4 w-4 mr-1" />
                  Mark all read
                </button>
              )}
              <button className={`p-2 rounded-full ${buttonHover} ${textMuted} hover:${darkMode ? 'text-gray-300' : 'text-gray-700'}`}>
                <Settings className="h-5 w-5" />
              </button>
            </div>
          </div>
          
          {/* Filter tabs */}
          <div className={`border-b ${borderColor}`}>
            <div className="flex overflow-x-auto py-3 px-4 gap-2">
              <button
                onClick={() => setFilter('all')}
                className={`px-4 py-2 text-sm font-medium rounded-full transition-colors ${
                  filter === 'all' 
                    ? `${darkMode ? 'bg-indigo-900/40 text-indigo-300' : 'bg-indigo-100 text-indigo-700'}` 
                    : `${textMuted} ${buttonHover}`
                }`}
              >
                All
              </button>
              <button
                onClick={() => setFilter('like')}
                className={`px-4 py-2 text-sm font-medium rounded-full flex items-center transition-colors ${
                  filter === 'like' 
                    ? `${darkMode ? 'bg-pink-900/30 text-pink-300' : 'bg-pink-100 text-pink-700'}` 
                    : `${textMuted} ${buttonHover}`
                }`}
              >
                <Heart className="h-4 w-4 mr-1" /> Likes
              </button>
              <button
                onClick={() => setFilter('comment')}
                className={`px-4 py-2 text-sm font-medium rounded-full flex items-center transition-colors ${
                  filter === 'comment' 
                    ? `${darkMode ? 'bg-blue-900/30 text-blue-300' : 'bg-blue-100 text-blue-700'}` 
                    : `${textMuted} ${buttonHover}`
                }`}
              >
                <MessageSquare className="h-4 w-4 mr-1" /> Comments
              </button>
              <button
                onClick={() => setFilter('follow')}
                className={`px-4 py-2 text-sm font-medium rounded-full flex items-center transition-colors ${
                  filter === 'follow' 
                    ? `${darkMode ? 'bg-green-900/30 text-green-300' : 'bg-green-100 text-green-700'}` 
                    : `${textMuted} ${buttonHover}`
                }`}
              >
                <User className="h-4 w-4 mr-1" /> Follows
              </button>
              <button
                onClick={() => setFilter('system')}
                className={`px-4 py-2 text-sm font-medium rounded-full flex items-center transition-colors ${
                  filter === 'system' 
                    ? `${darkMode ? 'bg-amber-900/30 text-amber-300' : 'bg-amber-100 text-amber-700'}` 
                    : `${textMuted} ${buttonHover}`
                }`}
              >
                <BookOpen className="h-4 w-4 mr-1" /> System
              </button>
            </div>
          </div>

          {/* Notifications count */}
          {!loading && (
            <div className={`px-6 py-3 ${textMuted} text-sm border-b ${borderColor}`}>
              {filteredNotifications.length} notification{filteredNotifications.length !== 1 ? 's' : ''}
              {unreadCount > 0 && filter === 'all' && (
                <span className={`ml-2 ${darkMode ? 'text-indigo-400' : 'text-indigo-600'}`}>
                  ({unreadCount} unread)
                </span>
              )}
            </div>
          )}

          {/* Notifications list */}
          <div className={`divide-y ${borderColor}`}>
            {loading ? (
              <>
                {[1, 2, 3].map((item) => (
                  <div key={item} className={`p-6 ${darkMode ? 'bg-gray-800' : 'bg-white'}`}>
                    <div className="animate-pulse flex space-x-4 w-full">
                      <div className={`rounded-full ${darkMode ? 'bg-gray-700' : 'bg-gray-200'} h-12 w-12`}></div>
                      <div className="flex-1 space-y-3 py-1">
                        <div className={`h-4 ${darkMode ? 'bg-gray-700' : 'bg-gray-200'} rounded w-3/4`}></div>
                        <div className={`h-4 ${darkMode ? 'bg-gray-700' : 'bg-gray-200'} rounded w-1/2`}></div>
                      </div>
                    </div>
                  </div>
                ))}
              </>
            ) : filteredNotifications.length === 0 ? (
              <div className={`p-12 text-center ${textMuted} flex flex-col items-center`}>
                <Bell className="h-12 w-12 mb-3 opacity-30" />
                <p className="text-lg font-medium mb-1">No notifications found</p>
                <p className="text-sm">Check back later for updates</p>
              </div>
            ) : (
              filteredNotifications.map((notification) => (
                <div 
                  key={notification.id} 
                  className={`p-5 ${darkMode ? 'hover:bg-gray-750' : 'hover:bg-gray-50'} flex group transition-colors ${
                    !notification.read ? (darkMode ? 'bg-blue-900/20' : 'bg-blue-50/80') : ''
                  }`}
                >
                  <div className="mr-4 mt-1">
                    {notification.userImage ? (
                      <div className={`h-12 w-12 rounded-full overflow-hidden ring-2 ${darkMode ? 'ring-gray-700' : 'ring-gray-200'}`}>
                        <img 
                          src={notification.userImage} 
                          alt="User" 
                          className="h-full w-full object-cover"
                        />
                      </div>
                    ) : (
                      <div className={`h-12 w-12 rounded-full ${darkMode ? 'bg-gray-700' : 'bg-gray-100'} flex items-center justify-center ring-2 ${darkMode ? 'ring-gray-600' : 'ring-gray-200'}`}>
                        {getNotificationIcon(notification.type)}
                      </div>
                    )}
                  </div>
                  <div className="flex-1">
                    <div className="flex items-baseline justify-between">
                      <p className={`text-sm ${!notification.read ? 'font-medium' : ''} ${textColor}`}>
                        {notification.message}
                      </p>
                      <span className={`text-xs ${textMuted} whitespace-nowrap ml-2`}>
                        {formatTimestamp(notification.timestamp)}
                      </span>
                    </div>
                    {notification.postTitle && (
                      <a 
                        href="#" 
                        className={`mt-2 text-sm ${darkMode ? 'text-indigo-400 hover:text-indigo-300' : 'text-indigo-600 hover:text-indigo-800'} block`}
                      >
                        {notification.postTitle}
                      </a>
                    )}
                  </div>
                  
                  {/* Action buttons - only visible on hover */}
                  <div className="ml-2 flex items-start space-x-1 opacity-0 group-hover:opacity-100 transition-opacity">
                    {!notification.read && (
                      <button 
                        onClick={() => markAsRead(notification.id)}
                        className={`p-1 rounded-full ${darkMode ? 'hover:bg-gray-700 text-gray-400 hover:text-blue-400' : 'hover:bg-gray-200 text-gray-500 hover:text-blue-600'}`}
                        title="Mark as read"
                      >
                        <Check className="h-4 w-4" />
                      </button>
                    )}
                    <button 
                      onClick={() => deleteNotification(notification.id)}
                      className={`p-1 rounded-full ${darkMode ? 'hover:bg-gray-700 text-gray-400 hover:text-red-400' : 'hover:bg-gray-200 text-gray-500 hover:text-red-600'}`}
                      title="Delete notification"
                    >
                      <Trash2 className="h-4 w-4" />
                    </button>
                  </div>
                </div>
              ))
            )}
          </div>
          
          {/* Footer */}
          {!loading && filteredNotifications.length > 0 && (
            <div className={`px-6 py-4 border-t ${borderColor} text-center`}>
              <button className={`text-sm ${darkMode ? 'text-indigo-400 hover:text-indigo-300' : 'text-indigo-600 hover:text-indigo-800'} font-medium`}>
                View more notifications
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}